* {
  margin: 0;
  padding: 0;
  text-shadow: 2px 2px 10px black;
  box-sizing: border-box;
}

body {
  background-color: #555;
  font-family: Righteous, Verdana, sans-serif;
}

.PageContainer {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  display: flex;
  flex-direction: column;
}

/* Day and Date */
.UpperBar {
  height: 12vh;
  background: linear-gradient(
    180deg, rgba(0,0,0,0.6) 0%, 
    rgba(0,0,0,0.5) 25%, 
    rgba(0,0,0,0.4) 50%,
    rgba(0,0,0,0.3) 75%, 
    rgba(0,0,0,0.24) 100%);
}

/* Time */
.UpperMiddleBar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  height: 38vh;
  background: linear-gradient(
    180deg, rgba(0,0,0,0.24) 0%, 
    rgba(0,0,0,0.15) 10%, 
    rgba(0,0,0,0.08) 20%,
    rgba(0,0,0,0.06) 30%,
    rgba(0,0,0,0.04) 40%,
    rgba(0,0,0,0.02) 50%,
    rgba(0,0,0,0.01) 60%,
    rgba(0,0,0,0.0) 70%);
}

/* Temp and Weather Icon */
.LowerMiddleBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 25vh;
  background: linear-gradient(
    180deg, rgba(0,0,0,0) 0%, 
    rgba(0,0,0,.02) 10%, 
    rgba(0,0,0,.04) 20%, 
    rgba(0,0,0,.06) 30%, 
    rgba(0,0,0,.08) 40%, 
    rgba(0,0,0,.129) 50%, 
    rgba(0,0,0,.186) 60%, 
    rgba(0,0,0,.258) 70%, 
    rgba(0,0,0,.348) 80%, 
    rgba(0,0,0,.46) 90%, 
    rgba(0,0,0,0.6) 100%);
}

/* Sunrise & Sunset */
.AstroContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}


/* Lower Bar */

.LowerBar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 25vh;
  background: rgba(0,0,0,0.75)
}

.AqiContainer {
  height: 100%;
  width: 10%;
}

.HourlyForecastContainer {
  height: 100%;
  width: 30%;
}

.DailyForecastContainer {
  height: 100%;
  width: 60%;
}

/* End Lower Bar */


/* Mobile screens */

@media only screen and (max-width: 600px) {

  /* Day and Date */
  .UpperBar {
    height: 8vh;
    background: linear-gradient(180deg, rgba(0,0,0,.6) 0%, rgba(0,0,0,.3) 100%);
  }

  /* Time */
  .UpperMiddleBar {
    height: 26vh;
    background: linear-gradient(180deg, rgba(0,0,0,.3) 0%, rgba(0,0,0,0) 100%);
  }

  /* Temp and Weather Icon */
  .LowerMiddleBar {
    height: 24vh;
    justify-content: space-around;
    background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,.3) 100%);
  }
  /* .AstroContainer {
    display: none;
  } */

