.DateContainer {
  display: flex;
  align-items: top;
  justify-content: space-between;
  padding: 0 10px;
}

.DayOfWeek {
  font-size: 8vw;
  z-index: 2;
}

.MonthAndDate {
  font-size: 8vw;
  z-index: 2;
}

/* Medium screens */
@media only screen and (max-width: 1200px) {
  .TimeContainer {
    font-size: 15vw;
  }
}

/* Medium-small screens */
@media only screen and (max-width: 800px) {
  .TimeContainer {
    font-size: 20vw;
  }
}

/* Mobile screens */
@media only screen and (max-width: 600px) {

  .DayOfWeek {
    font-size: 10vw;

  }
  .MonthAndDate {
    font-size: 10vw;

  }
}
.CustomMessage {
  padding-left: 20px;
  margin: 10px 10px 10px 0;
  font-size: 3vw;
  z-index: 2;
  max-width: 50%;
}
.TimeContainer {
  font-size: 22vw;
  position: relative;
  top: 6vh;
}

/* Medium screens */
@media only screen and (max-width: 1200px) {
  .TimeContainer {
    font-size: 18vw;
  }
}

/* Medium-small screens */
@media only screen and (max-width: 800px) {
  .TimeContainer {
    font-size: 20vw;
  }
}

/* Mobile screens */
@media only screen and (max-width: 600px) {
  .TimeContainer {
    font-size: 20vh;
  }
}
.TempContainer {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}


.Temperature {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  font-size: 15vw;
  line-height: .9; 
  padding-left: 5vw;
}

.DegreeSymbol {
  line-height: .7; 
  font-size: 8vw;
  position: relative;
  right: 5px;

}

.TempRangeContainer {
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  left: 1vw;
  bottom: 1vh;
}

.LowTemp, .HighTemp {
  font-size: 3.5vw;
  margin: 0 2px;
}

.TempRangeBar {
  align-self: center;
  height:2.5vw;
  width: 60%;
  background-color: black;
  border-radius: 2.5vw;
}

/* Mobile screens */
@media only screen and (max-width: 600px) {
  .Temperature {
    display: flex;
    align-items: flex-start;
    font-size: 25vw;
    position:static;
  }
  .DegreeSymbol {
    font-size: 15vw;
    position: relative;
    right: 1vw;
    top: 1vh;
  }

  .LowTemp, .HighTemp {
    font-size: 5.5vw;
    margin: 0 2px;
  }
  .TempRangeBar {
    height:4vw;
  }
}
.CurrentConditionsContainer {
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  right: 1vw;
}

.ForecastIcon {
  font-size: 15vw;
}

.ShortForecastText {
  font-size: 3.5vw;
  display: flex;
  justify-content: center;
  position: relative;
  bottom: 1vh;
}

/* Mobile screens */
@media only screen and (max-width: 600px) {
  .ForecastIcon {
    font-size: 20vw;
  }
  
  .ShortForecastText {
    font-size: 5vw;
    right: 0;
    bottom: .5vh;
  }
}
.AqiContainer {
  display: flex;
  flex-direction: column;
  padding: 0px 20px 15px 20px;
}

.AqiContainer-HorizontalBar {
  display: flex;
  flex-direction: row;
  height: 25%;
}

.AqiContainer-LeftInfo {
  flex: 1 1;
  display: flex;
